@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .nav-item {
    @apply text-lg p-2 px-4 hover:bg-lime-500 duration-300 hover:text-white;
  }
  .button {
    @apply bg-lime-500 px-4 py-2 rounded-lg hover:bg-lime-600 duration-300 hover:text-white;
  }
  .tab-item {
    @apply px-4 py-2 rounded-lg cursor-pointer border-transparent duration-300 hover:border-lime-500 border-2;
  }
  .cell {
    @apply p-5 text-center bg-transparent w-[100%] h-[100%] hover:border-lime-500 outline-lime-500 outline-4 m-0 border-2 focus:border-2 transition-colors duration-500 focus:bg-gray-300;
  }
  .table-cell {
    @apply border-[20px] border-white bg-gray-200 border-collapse animate-[realize_0.5s] hover:-translate-y-1 duration-300 hover:scale-105  ease-out p-0 h-16;
  }
  .table-content {
    @apply bg-transparent;
  }
  .field {
    @apply border-2 border-transparent p-10 rounded-lg hover:shadow-lg hover:border-lime-500 duration-500 hover:scale-105;
  }
  .cell-button {
    @apply hover:text-lime-600;
  }
  p {
    font-size: large;
  }
}

.react-tabs__tab--selected {
  background-color: #84cc16;
  color: white;
}
